<template>
    <div>
        <swiper :options="swiperOption" ref="mySwiper">
            
            <swiper-slide>
                <li class="li" v-for="item of items" :key="item.id">
                    <img :src="item.img" alt="">
                    <p>{{item.text}}</p>
                </li>
            </swiper-slide>
            <swiper-slide>
                <li class="li" v-for="item of item2s" :key="item.id">
                    <img :src="item.img" alt="">
                    <p>{{item.text}}</p>
                </li>
            </swiper-slide>


            
            <div class="swiper-pagination"  slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
export default {
    name:'HomeNavigation',
    data () {
        return{
            swiperOption:{

            },
            items:[
                {
                    id:'001',
                    img:'static/img/chi.png',
                    text:'吃',
                },
                {
                    id:'002',
                    img:'static/img/he.png',
                    text:'和',
                },
                {
                    id:'003',
                    img:'static/img/wan.png',
                    text:'玩',
                },
                {
                    id:'004',
                    img:'static/img/tj.png',
                    text:'乐',
                },
                                {
                    id:'005',
                    img:'static/img/fj.png',
                    text:'风景',
                },
                {
                    id:'006',
                    img:'static/img/lp.png',
                    text:'礼品',
                },
                {
                    id:'007',
                    img:'static/img/le.png',
                    text:'住宿',
                },
                {
                    id:'008',
                    img:'static/ic.png',
                    text:'首页',
                }
            ],
            item2s:[
                {
                    id:'001',
                    img:'static/img/chi.png',
                    text:'吃',
                },
                {
                    id:'002',
                    img:'static/img/he.png',
                    text:'和',
                },
                {
                    id:'003',
                    img:'static/img/wan.png',
                    text:'玩',
                },
                {
                    id:'004',
                    img:'static/img/tj.png',
                    text:'乐',
                },
                                {
                    id:'005',
                    img:'static/img/fj.png',
                    text:'风景',
                },
                {
                    id:'006',
                    img:'static/img/lp.png',
                    text:'礼品',
                },
                {
                    id:'007',
                    img:'static/img/le.png',
                    text:'住宿',
                },
                {
                    id:'008',
                    img:'static/ic.png',
                    text:'首页',
                }
            ],
        }
    }
}
</script>

<style scoped>

li{
    float: left;
    width: 25%;
    text-align: center;
    margin-top: 10px;
}
li img{
    width: 60%;
    height: 50px;
}
</style>





